import { useState } from 'react';
import { Spinner, Container, Nav, Navbar, Row, Col, Button } from 'react-bootstrap';
import data from './data.js' //data.js
import { Routes, Route, useNavigate } from 'react-router-dom';
import Detail from './pages/Detail.js';
import Cart from './pages/Cart.js';
import axios from 'axios' //axios 
import './App.css'
import FloatingBanner from './FloatingBanner.js'; //
import { useQuery } from 'react-query';

function App() {

	const [books, setBooks] = useState(data) //  state
	const [clickCnt, setClickCnt] = useState(0) //     state
	const [spin, setSpin] = useState(false) //ui state
	const navigator = useNavigate() //navigator 

	// 
	const handleSort = () => {
		let copy = [...books]
		copy.sort((a, b) => { //sort
			if (a.title < b.title) return -1 // a b  a  
			if (a.title > b.title) return 1 // a b  b  
			return 0 // a b   
		})
		setBooks(copy)
	}

	//react-query
	let result = useQuery('getUserNm',
		() => axios.get('https://codingapple1.github.io/userdata.json')
			.then((a) => {
				console.log("")
				return a.data
			}),
		{ staleTime: 2000 } //refetch 
	)

	return (
		<div>
			<Navbar bg="light" data-bs-theme="light">
				<Container>
					<a href="/react_shop"><img src={process.env.PUBLIC_URL + "/img/hao_logo1.png"} style={{ height: '70px', backgroundSize: 'cover' }} alt="logo" /></a>
					<Nav className="me-auto">
						<Nav.Link onClick={() => (navigator('react_shop'))}>Home</Nav.Link>
						<Nav.Link onClick={() => (navigator('react_shop/bestSeller'))}>BestSeller</Nav.Link>
						<Nav.Link onClick={() => (navigator('react_shop/new'))}>New</Nav.Link>
						<Nav.Link onClick={() => (navigator('react_shop/steadySeller'))}>SteadySeller</Nav.Link>
						<Nav.Link onClick={() => (navigator('react_shop/cart'))}>Cart</Nav.Link>
					</Nav>
					<Nav className='ms-auto'>{result.isLoading ? "" : ". " + result.data.name + " "}</Nav>
				</Container>
			</Navbar>

			<Routes>
				<Route path="/react_shop" element={
					<>
						<div className="main-bg"></div>
						<div>
							<div className="btn-area-right">
								<Button variant="primary" onClick={handleSort}></Button>
							</div>
							<Content books={books} />
							<div className='btn-area-center'>
								<Button variant="link"
									id={"more_" + 0 + clickCnt}
									onClick={() => {
										setSpin(true) //ui show
										setClickCnt(clickCnt + 1)
										axios.get(`https://codingapple1.github.io/shop/data${clickCnt + 2}.json`)
											.then((result) => {
												const res = result.data
												const copy = [...books, ...res]
												setBooks(copy)
												setSpin(false) //ui 
											})
											.catch(() => {
												console.log('')
												setSpin(false) //ui 
											})
									}}
									disabled={clickCnt >= 2 || spin}>
									{spin ? (
										<Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true" />
									) : ('')}
								</Button>
							</div>
						</div>
						<FloatingBanner />
					</>
				} />
				<Route path="/react_shop/detail/:id" element={<Detail books={books} />} />  {/**url  */}
				<Route path="/react_shop/cart" element={<Cart />} />
				<Route path="*" element={<div>404</div>} />
			</Routes>
		</div>
	);
}

function Content(props) {
	return (
		<Container>
			<Row>
				{props.books.map((book, i) => (
					<Col key={i} md={4}>
						<a href={`/react_shop/detail/${book?.id}`}><img src={process.env.PUBLIC_URL + `/img/book${book?.id}.PNG`} alt="" width={"90%"} /></a>
						<h4><a href={`/react_shop/detail/${book?.id}`}>{book?.title}</a></h4>
						<p>{book?.price}</p>
					</Col>
				))}
			</Row>
		</Container>
	)
}

export default App;
